import React, { useEffect, useState } from 'react';


export default function Cart({ cartItem, setCartItem, cart }) {
  const [total, setTotal] = useState();
  const [discountVal, setDiscountVal] = useState();
  const [totalAmt, setTotalAmtVal] = useState();
  const [savedItem, setSavedItem] = useState([]);

  useEffect(() => {
    let totalVal = 0;
    let discountVal = 0;
    let totalAmt = 0;
    cartItem.map((e) => {
      totalVal += e.maxPrice * 1 * e.itemCount;
      totalAmt += e.offerPrice * 1 * e.itemCount;
      discountVal += (e.maxPrice - e.offerPrice) * 1 * e.itemCount
    }
    )
    setTotal(totalVal);
    setDiscountVal(discountVal);
    setTotalAmtVal(totalAmt);
  })
  const saved = (e) => {
    if (savedItem.indexOf(e) !== -1) {
      return;
    }
    setSavedItem([...savedItem, e]);

  }
  const remove = (id) => {
    const val = cartItem.filter((e) => e.id !== id);
    setCartItem(val);
  }
  const removeSaved = (id) => {
    const val = savedItem.filter((e) => e.id !== id);
    setSavedItem(val);
  }

  const inc = (e) => {
    let i = cartItem.indexOf(e);
    let arr = cartItem;
    if (e.itemCount === 3) {
      return;
    } else {
      arr[i].itemCount++;
      setCartItem([...arr]);
    }
  }
  const dec = (e) => {
    let i = cartItem.indexOf(e);
    let arr = cartItem;
    if (e.itemCount === 1) {
      return;
    } else {
      arr[i].itemCount--;
      setCartItem([...arr]);
    }
  }


  return (
    <div className='cart'>
      <h2 className='cart-title'>Tex Valley Cart</h2>

      <div className='cart-wrapper'>
        <div className="cart-items-wrapper">
          <h2>Cart&#40;{cartItem.length}&#41;</h2>

          {
            cartItem.map((e) =>
              <div className="cart-items" >
                <div>
                  <img src={e.img} alt="" />
                  <div className='plus-minus'>
                    <button onClick={() => dec(e)}><span>-</span></button>
                    <p>{e.itemCount}</p>
                    <button onClick={() => inc(e)}><span>+</span></button>
                  </div>
                </div>
                <div className='desc'>
                  <h3>{e.name}</h3>
                  <p>Dark, Marroon Bordered </p>
                  <p>Brand : Velvatta</p>
                  <div className='price'> <span>Rs{e.maxPrice * e.itemCount}</span> <span>Rs{e.offerPrice * e.itemCount}</span> <span>{e.discount}&#37; off</span></div>
                  <button onClick={() => { saved(e); remove(e.id) }}>SAVE FOR LATER</button>
                  <button onClick={() => remove(e.id)}> REMOVE</button>
                </div>
              </div>
            )
          }
          <div className='place-order'>
            <button>PLACE ORDER</button>
          </div>
          <div className="saved">
            <h2>Saved for later&#40;{savedItem.length}&#41;</h2>
            {
              savedItem.map((e) =>
                <div className="cart-items" >
                  <div>
                    <img src={e.img} alt="" />
                  </div>
                  <div className='desc'>
                    <h3>{e.name}</h3>
                    <p>Dark, Marroon Bordered </p>
                    <p>Brand : Velvatta</p>
                    <div className='price'> <span>Rs{e.maxPrice}</span> <span>Rs{e.offerPrice}</span> <span>{e.discount}&#37; off</span></div>
                    <button onClick={() => { cart(e); removeSaved(e.id) }}>MOVE TO CART</button>
                    <button onClick={() => removeSaved(e.id)}> REMOVE</button>
                  </div>
                </div>
              )
            }
          </div>
        </div>
        <div className="summary">
          <h2>PRICE DETAILS</h2>
          <div><span>Price&#40;{cartItem.length} items&#41;</span><span>{total}</span></div>
          <div><span>Discount</span><span>{discountVal}</span></div>
          <div><span>Delivery Charges</span><span>Free</span></div>
          <div><span>Total Amount</span><span>{totalAmt}</span></div>
          <p>You wil save {discountVal} on this order</p>
        </div>
      </div>


    </div>
  )
}
