import Home from './pages/Home';
import {BrowserRouter,Routes,Route} from 'react-router-dom';
import Product from './pages/Product';
import './App.css';
import React, { useState } from 'react';
import Navbar from './components/Navbar';
import Cart from './pages/Cart';
import Notify from './components/Notify';



const App = ()=>{
  const [cartItem,setCartItem]=useState([]);


  const cart =(val)=>{
  
    if( cartItem.indexOf(val) !== -1){
      return;
    }
    setCartItem([...cartItem,val]);
  }
  
 
  return(
   <>
   <Notify/>
   <BrowserRouter>
   <div className="navbar">
   <Navbar cartItem={cartItem} setCartItem={setCartItem}/>
   </div>
   <Routes>
      <Route index element={<Home cart={cart} cartItem={cartItem} setCartItem={setCartItem}/>}/>
      <Route path='Product' element={<Product cart={cart} cartItem={cartItem} setCartItem={setCartItem}/>}/>
      <Route path='Cart' element={<Cart cartItem={cartItem} setCartItem={setCartItem} cart={cart} />}/>
   </Routes>
   </BrowserRouter>
   
   </>
  );
}

export default App;