import React from "react";
import { Link, Outlet } from 'react-router-dom';
import { AiOutlineSearch, AiOutlineShoppingCart,AiOutlineHeart } from 'react-icons/ai';
import { MdOutlineAccountCircle} from 'react-icons/md';


const Navbar = ({ cartItem, setCartItem }) => {



    return (
        <>
            <nav className="container">
                <div className="logo ">
                    <p> Tex valley</p>
                </div>
                <div className="navlinks">
                    <ul>
                        <li><Link to='/'  >Home</Link></li>
                        <li><Link to='/Product'>Festive drop</Link></li>
                        <li><Link to='/Product'>New Drops</Link></li>
                        <li><Link to='/Product'>Sarees</Link></li>
                        <li><Link to='/Product'>Categories</Link></li>
                        <li><Link to='/Product'>Wedding vibes</Link></li>
                    </ul>
                </div>
                <div className="icons">
                    <ul>
                        <li><Link to=''><AiOutlineSearch /></Link></li>
                        <li><Link to=''><AiOutlineHeart  /></Link></li>
                        <li className="cart-icon"><Link to='/Cart'><AiOutlineShoppingCart /><span>{cartItem.length}</span></Link></li>
                        <li ><Link to=''><MdOutlineAccountCircle /></Link></li>
                    </ul>
                </div>
            </nav>

            <Outlet />
        </>
    );
}

export default Navbar;





