import React from 'react';
import {AiFillStar} from 'react-icons/ai';
import { Herodata } from '../data/Herodata';
import {AiOutlineShoppingCart} from 'react-icons/ai';
import {Link,Outlet} from 'react-router-dom';




export default function Card({cart,cartItem,setCartItem}) {

  const {data} = Herodata;


const offer = (x,y)=>{
    let b=0;
    b=x-(x*y)/100;
    return(Math.round(b));
}
data.map((e) =>   
e.offerPrice =offer(e.maxPrice,e.discount)
)

    return (
        <div className='cardContianer'>
             {
            data.map((e) =>   

                <div className="card" key= {e.id}>
                    <img src={e.img} alt="" />
                    <h2>{e.name}</h2>
                    <div className='review'>
                        <div>	 {e.rating}<AiFillStar/></div>
                        <div> &#40;{e.reviewCount}&#41; </div>
                        <div >{e.discount} &#37; off </div>
                    </div>
                    <p className='price'> <span>Rs {e.maxPrice}</span> <span>Rs {e.offerPrice}</span> <Link onClick={()=>cart(e)} to="/Cart" >Add to <AiOutlineShoppingCart/> </Link> </p>
                </div>
            )
            }
            <Outlet/>
        </div>
        
    )
}
