import React, { useState } from 'react';
import {CgCloseO } from 'react-icons/cg';


export default function Notify() {
    const [notifyStyle,setNotifyStyle]=useState('notify');
    const close=()=>{
        setNotifyStyle(notifyStyle + " hide");
    }

  return (
    <div className={notifyStyle}>
      <p>Sign up and get Rs.500 off on your first purchase. Use code "TEXVALLEY10" *tnc apply</p>
      <CgCloseO onClick={close}/>
    </div>
  )
}
