import React from 'react'
import BestNewArrivals from '../components/BestNewArrivals'
import Footer from '../components/Footer'
import HeroSlider from '../components/HeroSlider'
import Newsletter from '../components/Newsletter'
import Card from '../components/Card';

export default function Home({cart,cartItem,setCartItem}) {
  return (
    <div className='home'>
      <HeroSlider/>
      <BestNewArrivals/>
      <div className='product'>
        <Card cart={cart} cartItem={cartItem} setCartItem={setCartItem}/>
    </div>
      <Newsletter/>
      <Footer/>
    </div>
  )
}
